import EventManager from "../../../eventManager/EventManager";
import { GameEvents } from "../../../gameEvents/GameEvents";
import { BaseState } from "../../../state-machine/BaseState";
import { GameStates } from "../../gameStateMachine/StatesEnum";


export class BuildState extends BaseState {

  begin(data?: any) {
    EventManager.dispatch(GameEvents.BUILD_START);
    EventManager.add(GameEvents.BUILD_END, this.end);
  }

  end = (): void => {
    this.stateMachine.setState(GameStates.POSITIONING);
  }

  cleanUp(): void {

  }
}